import React from "react";
import { Table } from "react-bootstrap";
import { connect } from "react-redux";

function Cart(props) {
  return (
    <>
      <div>
        <Table responsive>
          <thead>
            <tr>
              <th>#</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {props.state.map((e, i) => {
              return (
                <>
                  <tr key={i}>
                    <td>{e.id}</td>
                    <td>{e.name}</td>
                    <td>{e.quan}</td>
                    <td>
                      <button
                        onClick={() => {
                          props.dispatch({ type: "" });
                        }}
                      >
                        {" "}
                        +{" "}
                      </button>{" "}
                      <button
                        onClick={() => {
                          props.dispatch({ type: "" });
                        }}
                      >
                        {" "}
                        -{" "}
                      </button>
                    </td>
                  </tr>
                </>
              );
            })}
          </tbody>
        </Table>

{props.alert === true ? (<div className="my-alert2">
            <p>   20%</p>
            <button onClick={()=>{
                props.dispatch({type : ""})
            }}></button>
        </div>)

:''}

        
      </div>
    </>
  );
}

function stateprops(state) {
  return {
    state: state.reducer,
    alert : state.reducer2
  };
}

export default connect(stateprops)(Cart);
//export default Cart;
